/************ HEADER  ************/
.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header a.active {
  color: #3385d7 !important;
}

.header #imgDiv img {
  width: 8rem;
}

.header.sticky #imgDiv img {
  filter: brightness(0) invert(1);
}

.header.sticky {
  background-color: #000000;

}

.header ul a {
  font-size: 14px;
}

/************ HEADER ENDS  ************/


/************ FOOTER  ************/
footer#footer {
  background-image: url(./images/dentist4-footer-bg.webp);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  background-color: #000000;
}

footer #footer-top {
  padding: 100px 0px;
}

footer #footer-top section h4 {
  font-size: 30px;
  line-height: 40px;
  font-weight: 600;
  letter-spacing: 0px;
  color: white;

}

footer #footer-top section h5 {
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;
  letter-spacing: 0px;
  color: #3385d7;
}

footer #footer-top section p {
  font-size: 18px;
  color: white;

}

footer #footer-top section a {
  font-size: 18px;
  color: #3385d7;
  text-decoration: none;
  font-weight: 600;
  display: block;
}

footer #footer-top section a:hover {
  font-size: 18px;
  color: white;

}

footer #footer-top section img {
  width: 9rem;
  /* filter: brightness(0) invert(1); */
}

footer #footer-bottom {
  padding: 22px 0px 22px 0px;
  margin-top: 10px;
  font-size: 18px;
  display: flex;
  font-weight: bold;
  color: white;
  justify-content: center;
  flex-wrap: wrap;
  border-top: 1px solid rgba(255, 255, 255, .1);
  text-align: center;
}

footer .whatsappICON {
  position: fixed;
  font-size: 30px;
  color: white;
  background-color: rgb(22, 240, 14);
  border-radius: 50%;
  padding: 5px;
  bottom: 15px;
  left: 15px;
}

/************ FOOTER ENDS  ************/

