/************ HOME LANDING PAGE   ************/

/******** header   ********/

#homePage header #leftSideHeader {
  height: 960px;
  /* width: 100%; */
  position: relative;
  display: grid;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  justify-items: end;
  text-align: end;
}

#homePage header #leftSideHeader::before {
  content: "";
  background: url(./images/dentist4-slider-bg.webp) no-repeat center center /cover;
  height: 100%;
  /* width: 100%; */
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  top: 0;
  z-index: -1;
  background-color: #000000;
  transform: scaleX(-1);
  /* filter: brightness(0.4); */

}

#homePage header #leftSideHeader h6 {
  font-weight: 700;
  color: rgb(51, 133, 215);
}

#homePage header #leftSideHeader h1 {
  font-size: 55px;
}

#homePage header #leftSideHeader p {
  width: 40%;
}

#homePage header #leftSideHeader button {
  background-color: rgb(51, 133, 215);
  margin-top: 40px;
  font-weight: 600;
  font-size: 13px;
  padding: 18px 43px;
  border-radius: 50px;
  border: none;
  color: white;
  transition: 0.5s;
}

#homePage header #leftSideHeader button:hover {
  background-color: #78b1ec;
}

#homePage header #rightSideHeader {
  padding-top: 200px;
  padding-bottom: 100px;
  background-color: #3043b8;
  background-image: url(./images/dentist4-slider-pic1.webp);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

/******** services   ********/

#homePage #serviceSec .service {
  /* height: 400px; */
  position: relative;
  overflow: hidden;
}

#homePage #serviceSec .service::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  filter: blur(0.4px) brightness(0.7);
}

#homePage #serviceSec .service1::before {
  background: url(./images/services/implant.jpg) no-repeat center center/cover;
}

#homePage #serviceSec .service2::before {
  background: url(./images/services/implant2.jpg ) no-repeat center center/cover;
}

#homePage #serviceSec .service3::before {
  background: url(./images/services/zirkon.jpg) no-repeat center center/cover;
}

#homePage #serviceSec .service4::before {
  background: url(./images/services/ct.jpg) no-repeat center center/cover;
}

#homePage #serviceSec .service5::before {
  background: url(./images/services/5photo.jpg) no-repeat center center/cover;
}

#homePage #serviceSec .service6::before {
  background: url(./images/services/panorami.jpg) no-repeat center center/cover;
}

#homePage #serviceSec .service:hover .mainTextDiv:before {
  height: 100%;
  background-position: top;
  background-image: linear-gradient(0deg, #14a966, transparent 100%);
}

#homePage #serviceSec .service .mainTextDiv {
  transform: translateY(300px);
  transition: 1000ms;
}

#homePage #serviceSec .service .mainTextDiv:before {
  content: "";
  z-index: -1;
  inset: 0;
  position: absolute;
  background-image: linear-gradient(180deg, #14a966, transparent 100%);
  /* background-position: bottom; */
  transition: background-position 1s, height 1s;
  height: 40%;
  top: 3px;
}

#homePage #serviceSec .service:hover>.mainTextDiv {
  transform: translateY(0px);
}

#homePage #serviceSec .service .textDiv {
  /* opacity: 0; */
  transition: 500ms;
}

#homePage #serviceSec .service:hover .textDiv {
  /* opacity: 1; */
}

#homePage #serviceSec .service button::before {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  height: 2px;
  margin-top: -1px;
  content: '';
  z-index: -1;
  -webkit-transition: -webkit-transform 0.3s, opacity .3s;
  -moz-transition: -moz-transform 0.3s, opacity .3s;
  transition: transform 0.3s, opacity .3s;
  pointer-events: none;
  background: #3385d7
}

#homePage #serviceSec .service button::after {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 2px;
  margin-top: -1px;
  content: '';
  z-index: -1;
  -webkit-transition: -webkit-transform 0.3s, opacity .3s;
  -moz-transition: -moz-transform 0.3s, opacity .3s;
  transition: transform 0.3s, opacity .3s;
  pointer-events: none;
  background: #3385d7
}

button:hover::before {
  transform: rotate(45deg);
  top: 19px !important;
  opacity: 0.5;
  background: #3f3c3c !important;
}

button:hover::after {
  transform: rotate(-45deg);
  bottom: 18px !important;
  opacity: 0.5;
  background: #3f3c3c !important;
}

/*******  testimonials *********/

#homePage #testimonSec .testiImg img {
  /* height: 700px; */
}

/****** patient experiences *****/
#homePage #expSec {
  background-color: #3043b8;
  background-image: url(./images/dentist4-section-bg8.webp);
  background-repeat: no-repeat;
  background-position: center top;
}

/****** patient transformations *****/

#homePage #transformSec .transform-img-mainDiv {
  width: 623px;
  transition: 600ms;
  height: 212px;
}

#homePage #transformSec .transform-img-mainDiv:hover {
  /* filter: brightness(0.7); */
}


#homePage #transformSec .transform-img-mainDiv:hover::before,
#homePage #transformSec .transform-img-mainDiv:hover::after {
  opacity: 1;
}

#homePage #transformSec .transform-img-mainDiv::before {
  content: "Before";
  color: #fff;
  opacity: 0;
  transition: 600ms;
  font-size: 13px;
  letter-spacing: .1em;
  z-index: 2;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  line-height: 38px;
  padding: 0 20px;
  border-radius: 2px;
  top: 100px;
  left: 10px;
}

#homePage #transformSec .transform-img-mainDiv::after {
  content: "After";
  opacity: 0;
  transition: 600ms;
  color: #fff;
  font-size: 13px;
  letter-spacing: .1em;
  z-index: 2;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  line-height: 38px;
  padding: 0 20px;
  top: 100px;
  right: 0;
  border-radius: 2px;
  right: 10px;
}

#homePage #transformSec .transform-images {
  object-fit: fill !important;
}

/************ HOME LANDING PAGE ENDS   ************/

