#contactPage header {
  height: 960px;
  /* width: 100%; */
  position: relative;
  display: grid;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  justify-items: end;
  text-align: end;
}


#contactPage header::before {
  content: "";
  background: url(./images/dentist4-slider-bg.webp) no-repeat center center /cover;
