}

#appointFormSec input::-webkit-outer-spin-button,
#appointFormSec input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#formSec input[type=text],
#formSec input[type=email],
#formSec input[type=number],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 30px;
  box-sizing: border-box;
  margin-top: 6px;
  font-size: 14px;
  margin-bottom: 16px;
  resize: vertical;

}

#formSec input:focus,
#formSec textarea:focus {
  color: #3385d7;
  background-color: rgba(212, 228, 246, 1) !important;
  border-color: #d5e5ee;
  border: none !important;
  outline: none !important;
  caret-color: #1f2e88;

}

#formSec input[type=submit] {
  background-color: #3385d7;
  padding: 16px 42px;
  border-width: 0px;
  transition: 300ms;
  border-radius: 36px;
  font-size: 14px;
  font-weight: bold;
  color: white;
  caret-color: #1f2e88;
  border: none;
  cursor: pointer;
}

#formSec input[type=submit]:hover {
  background-color: #5ea0e8;
}

#formSec form {
  padding: 80px 40px 0px;
  background-color: #ffffff;
  margin-top: -60px;
  border-radius: 50px;
  box-shadow: 2px 44px 32px 2px rgba(0, 0, 0, 0.02);
  border-radius: 60px;
  transform: translateY(-35px);

}

/************ CONTACT PAGE ENDS   ************/


