/************ APPOINTMENT PAGE STARTS   ************/
#appointFormSec #nxtBtn {
  background-color: rgb(51, 133, 215);
  margin-top: 40px;
  font-weight: 600;
  padding: 10px 30px;
  border-radius: 50px;
  border: none;
  color: white;
  transition: 0.5s;
}

#appointFormSec #prvBtn {
  background-color: #8d8d8d;
  margin-top: 40px;
  font-weight: 600;
  padding: 10px 30px;
  border-radius: 50px;
  border: none;
  color: white;
  transition: 0.5s;
}

#homePage #appointFormSec #serviceSec .service .mainTextDiv {
  transform: translateY(130px);
  transition: 1000ms;
}

#homePage #appointFormSec #serviceSec .service .mainTextDiv:before {
  height: 200%;
}

#homePage #appointFormSec #serviceSec .service:hover,
#homePage #appointFormSec #DtLocSec .maps:hover {
  filter: brightness(0.8);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
}

#homePage #appointFormSec .SelectedService {
  filter: brightness(0.8);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
  border: solid #1f2e88;

}

#homePage #appointFormSec #DtLocSec .mapsDiv:before {
  content: "";
  position: absolute;
  z-index: 10;
  height: 100%;
  width: 100%;
  /* background-color: #000000; */
  cursor: pointer;
  transition: 300ms;
}

#homePage #appointFormSec #DtLocSec .mapsDiv:hover:before {
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

#homePage #appointFormSec #formSec form {
  transform: translateY(0px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);

}

#appointFormSec .step1.active,
#appointFormSec .step2.active,
#appointFormSec .step3.active {
  background-color: #3385d7;
}

#appointFormSec .step1.completed,
#appointFormSec .step2.completed,
#appointFormSec .step3.completed {
  background-color: #1f2e88;
}



