/* media query */
@media (width < 565px) {
  #homePage #transformSec .transform-img-mainDiv {
    width: auto;
    transition: 600ms;
    height: 212px;
  }

  footer #footer-bottom {
    padding: 22px 10px 22px 10px;
  }

  .testiImg {
    display: none;
  }

  #homePage header #leftSideHeader {
    height: auto;
  }

  #homePage header #leftSideHeader p {
    width: auto;
  }

  .vidIframe {
    height: 440px;
  }
}

@media (width < 500px) {
  .vidIframe {
    height: 400px;
  }

}

@media (width < 450px) {
  .vidIframe {
    height: 350px;
  }

  #homePage #serviceSec .service::before {
    background-size: contain;
  }

}

@media (width < 400px) {
  .vidIframe {
    height: 300px;
  }

}

@media (width < 350px) {
  .vidIframe {
    height: 250px;
  }

}

@media (width < 300px) {
